.login_container {
	width: 100%;
	min-height: 100vh;
	background-color: #f5f5f5;
	display: flex;
	align-items: center;
	justify-content: center;
}

.login_form_container {
	width: 900px;
	height: 900px;
	display: flex;
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}

.left {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: white;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
}

.form_container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form_container h1 {
	font-size: 30px;
	margin-top: 0;
}

.input {
	outline: none;
	border: none;
	width: 370px;
	padding: 10px;
	border-radius: 10px;
	background-color: #edf5f3;
	margin: 5px 0;
	font-size: 14px;
}

.error_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #f34646;
	color: white;
	border-radius: 5px;
	text-align: center;
}

.right {
	flex: 2; 
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center; 
	background-color: #eaeaea;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
	background-image: url("pet-background4.jpg");
	background-repeat: no-repeat; 
	background-position: bottom;
  	/* background-size: cover;  */
}

.right h1 {
	display: flex;
	align-items: top;
	margin-top: 0;
	color: black;
	font-size: 27px;
	align-self: center;
}


.login_btn {
	border: none;
	outline: none;
	padding: 12px 0;
	background-color: #6345d1;
	color: white;
	border-radius: 20px;
	width: 180px;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
	margin: 10px;
}

.signup_btn{
	border: none;
	color: #2342a7;
	outline: none;
	padding: 12px 0;
	background-color: white;
	text-decoration: underline;
	border-radius: 20px;
	width: 100px;
	font-weight: bold;
	font-size: 15px;
	cursor: pointer;
}

.left h2 {
	margin-top: 0;
	font-weight: bold;
	color: black;
	font-size: 15px;
	align-self: center;
}

